.login {
  width: 400px;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.login input {
  padding: 5px;
  margin-top: 10px;
  border: none;
  border-bottom: 1px solid rgb(24, 66, 121);
  outline: none;
}
.login input:hover {
  border-bottom: 1px solid rgb(36, 96, 175);
}

.login button {
  padding: 15px 25px;
  margin-top: 20px;
  background-color: rgb(24, 66, 121);
  color: white;
  border: none;
  border-radius: 8px;
  width: 110px;
  cursor: pointer;
}
.login button:hover {
  background-color: rgb(36, 96, 175);
}
