.messages {
  width: 60%;
  display: flex;
  flex-direction: column;
  background-color: rgba(185, 185, 185, 0.815);
  margin-right: 30px;
  padding-bottom: 30px;
}

.messages-list {
  width: 100%;
  height: 75vh;
  overflow-y: scroll;
  background-color: rgba(244, 248, 250, 0.931);
}

.messages-list .ul {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.messages-list .ul .li {
  box-sizing: border-box;
  color: white;
  margin: 10px;
  padding: 0 10px;
  font-size: 20px;
  width: 98%;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: flex-start;
}

.messages-list .ul .li div {
  background-color: #6dc3cc;
  display: table;
  padding: 10px 20px;
  border-radius: 4px;
  max-width: 500px;
}

.messages-list .ul .author {
  font-size: 15px;
  font-style: italic;
  color: #f8f8f8;
}

.myMessage {
  display: flex;
  flex-direction: column;
  align-items: flex-end !important;
}

.myMessage div {
  background-color: #007d98 !important;
}

.send-message {
  box-sizing: border-box;
  width: 94%;
  padding: 10px 20px;
  margin: 0 auto;
  margin-top: 30px;
}

.send-message input {
  background-color: transparent;
  padding: 15px 25px;
  margin-top: 10px;
  margin-right: 10px;
  border: none;
  border-bottom: 2px solid #007d98;
  outline: none;
  width: 80%;
}

.send-message input:hover {
  border-bottom: 1px solid #6dc3cc;
}

.send-message button {
  padding: 15px 25px;
  margin-top: 20px;
  background-color: #007d98;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  width: 110px;
  cursor: pointer;
}

.send-message button:hover {
  background-color: #6dc3cc;
}

.channels {
  width: 240px;
  box-sizing: border-box;
  color: black;
}

.channels .user {
  margin: 0 auto 40px auto;
}

.channels ul {
  text-decoration: none;
  list-style: none;
  padding-top: 10px;
}

.channels ul li {
  margin: 4px 0;
  padding: 6px;
  border-radius: 5px;
}

.channels ul li:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.096);
}

.channel-buttons {
  background-color: transparent;
  width: 38px;
  margin: 0 10px;
  padding: 5px 10px;
  font-size: 26px;
  font-weight: bolder;
  cursor: pointer;
  color: #007d98;
  border: none;
  border-radius: 8px;
}

.channel-buttons:hover {
  background-color: #dddfe2;
  font-size: 26px;
}

.active {
  color: #0f0e0e;
  background-color: #6dc3cc;
  padding: 10px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
  color: #ffffff;
  height: 100vh;
  width: 100vw;
}

.App {
  width: 85vw;
  height: 85vh;
  margin: auto;
  margin-top: 5vh;
  display: flex;
  justify-content: center;
}

.error {
  color: #ff0404;
}

p {
  color: #01080e;
}

.author {
  font-size: 15px;
  font-style: italic;
}
