.login {
  width: 400px;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.login input {
  padding: 5px;
  margin-top: 10px;
  border: none;
  border-bottom: 1px solid rgb(24, 66, 121);
  outline: none;
}
.login input:hover {
  border-bottom: 1px solid rgb(36, 96, 175);
}

.login button {
  padding: 15px 25px;
  margin-top: 20px;
  background-color: rgb(24, 66, 121);
  color: white;
  border: none;
  border-radius: 8px;
  width: 110px;
  cursor: pointer;
}
.login button:hover {
  background-color: rgb(36, 96, 175);
}

.login-background {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 600px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px rgb(30, 30, 31);
  padding: 36px;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

